<template>
  <div class="order-content">
    <TheCompiler :tabs='tabs' @changeTabs='handleChangeTabs'></TheCompiler>
    <TheOrderList :orderList='filterOrderList'></TheOrderList>
  </div>
</template>

<script>
import TheCompiler from './TheCompiler.vue'
import TheOrderList from './TheOrderList.vue'
export default {
  data () {
    return {
      tabs: [
        {
          tabtxt: '已出单',
          active: false,
		  status: 2
        },
        {
          tabtxt: '保障中',
          active: false,
		  status: 3
        },
        {
          tabtxt: '全部',
          active: true,
		  status: 0
        }
      ],
      orderList: []
    }
  },
  components: {
    TheCompiler,
    TheOrderList
  },
  methods: {
    handleChangeTabs (tabParam) {
      this.tabs.find(tab => tab.active).active = false
      tabParam.active = true
    }
  },
  computed: {
    filterOrderList () {
      let status = this.tabs.find(tab => tab.active).status
      return this.orderList.filter(order => {
        if (status === 0) {
          return true
        }
        return order.status == status
      })
    }
  },
  created(){
	  this.$api.personCenter.getGroupInsuranceList({
	  	page: 1,
	  	limit: 10
	  }).then((data)=>{
	  	console.log(data)
		if(data.code===1){
			let orderList = [];
			orderList = data.data.data
			this.orderList = orderList
		}
	  })
  }
}
</script>

<style scoped="scoped" lang="scss">
  .order-content {
    background: #fff;
    box-shadow: 0 4px 12px 0 rgba(219, 219, 219, 0.25);
  }
</style>
